@media (min-width: 992px) {
    #post-list {
        padding-top: 190px;
    }

    .search-form-posts {
        position: fixed;
        top: 70px;
        left: 0;
        right: 0;
        z-index: 9;
        padding: 16px 0;
        height: 80px;
        background-color: #fff;
        border-bottom: 1px solid #eee;

        .btn-actions {
            display: none;
        }

        .pc-search-form {
            display: flex;
            align-items: center;

            .label {
                padding: 0 30px;

                label {
                    margin-bottom: 0;
                    color: #566985;
                    font-size: 14px;
                    font-weight: 500;
                }
            }

            .form-group {
                margin-bottom: 0;
                padding-left: 15px;
                padding-right: 15px;
            }

            .form-control {
                padding: 16px 20px;
                height: 48px;
                background: #f5f7fb;
                color: #7d93b2;
                font-size: 12px;
                border: 1px solid #e5e7f2;
                border-radius: 4px;
            }

            .search-content {
                flex: 1;
                display: flex;
                align-items: center;

                .q,
                .price-wrap,
                .multiselect-localtion,
                .category,
                .submit-form {
                    width: 20%;
                }
            }

            .advanced-search-wrap {
                padding: 15px;
                cursor: pointer;
                color: #566985;
                font-size: 10px;
                font-weight: 500;
                text-transform: uppercase;
            }

            .submit-form {
                button {
                    background: #3270fc;
                    color: #fff;

                    &:hover {
                        background: linear-gradient(to right, #3c6afd, #57c7fa);
                    }
                }
            }

            #price-zone2 {
                position: relative;
                cursor: pointer;

                #price-display {
                    border: none;
                    background-color: transparent;
                    outline: none;
                    cursor: grab;
                }

                .dropdown-menu.price-dropdown {
                    width: 100%;
                    padding: 30px 20px;
                    box-sizing: border-box;

                    .ui-widget-header {
                        background: #3270fc;
                    }

                    .ui-slider-handle {
                        border-radius: 50%;
                    }

                    .price-inputs {
                        margin-bottom: 20px;
                        display: flex;

                        input {
                            display: flex;
                            width: calc(100% / 2 - 5px);
                            height: 30px;
                            padding: 6px 12px;
                            border: 1px solid #e0e1e6;
                            border-radius: 6px;

                            &:first-child {
                                margin-right: 10px;
                            }
                        }
                    }
                }
            }
        }
    }

    .sidebar-search-form {
        display: none;
    }

    .posts {
        .row {
            margin-left: -8px;
            margin-right: -8px;

            [class^="col-"] {
                padding-left: 8px;
                padding-right: 8px;
            }
        }
    }
}
